        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'The Future of Art',
        authors: ['J Rosenbaum'],
        abstract: 'Most people are aware of the impact machine learning will have on jobs, on the future of research and autonomous machines, but few seem to be aware of the future role machine learning could play in the creative arts, in visual art and music. What will art be like when artists and musicians routinely work collaboratively with machines to create new and interesting artworks? What can we learn from art created using neural networks and what can we create? From the frivolous to the beautiful what does art created by computers look like and where can it take us?\r\n\r\nThis talk will explore magenta in tensorflow and neural style in caffe, google deep dream, next Rembrandt, and convolutional neural networks. I will look into some of the beautiful applications of machine learning in art and some of the ridiculous ones as well. From paint samples to autonomous works I will explore the joys and horrors of machine learning for artistic purposes. I will also touch on the emotional impact of digital art including the uncanny valley.',
