        name: 'History rhyming in the kernel community and beyond',
        authors: ['Jonathan Corbet'],
        abstract: '"History never repeats itself, but it often rhymes" goes a quote often attributed to Mark Twain.  In this talk, Jonathan Corbet brings over 35 years of experience in the free-software development community to bear on the questions of how we got to where we are, and where we may be headed in the future.  We set out to change the world, and we succeeded beyond our wildest dreams.  So why are we not happier with what we got, and what does the past tell us about how we should be building a better future?',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/29/',
