        contact: ['redacted'],
        name: 'Creating a Linux Powered Robot',
        authors: ['Simon Lees'],
        abstract: 'Often as software engineers / developers the first thing we do when we want to start a cool new project is start writing code. In reality what we should do is to take a step back and look at what the people who went before us have already done, open source software provides a wonderful platform to build cool things with very little effort by utilizing existing code. \r\n\r\nI will use the experiences and lessons I have learned from building a Linux powered robot to illustrate this, I will go through my design emphasizing the design decisions taken to further illustrate this point while also touching on other systems engineering concepts such as modular design and using open protocols to further allow reuse and changes.',
