        track: null,
        conf_key: 7,
        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Everyone gets a pony!',
        authors: ['Kristoffer Gr\u00f6nlund'],
        abstract: 'This talk is either a free horse giveaway, or a talk about a programming language called Pony.\r\n\r\nMemory safety in low-level programming languages is a topic that has received a lot of attention in the last few years, with languages like Rust, Julia and Swift that promise high performance and low runtime overhead coupled with a safer memory model than the "you\'re on your own" model of C and C++.\r\n\r\nPony is a programming language which draws inspiration from a wide variety of existing languages like Erlang, C and Python while at the same time incorporating some recent academic language research in the form of capability-based security. The resulting language compiles to native code, is compatible with C, highly concurrent and at the same time type safe, memory safe and data-race free. Most importantly, it is a joy to use!\r\n\r\nIn this talk I will provide an introduction to Pony together with some examples of what it looks like, and also give my own personal reasons for being interested in yet another new programming language.',
