        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'The Meson Build System - 4+ years of work to become an overnight success',
        authors: ['Jussi Pakkanen'],
        abstract: 'In the last year many core projects in the Linux ecosystem have transitioned to use the Meson build system. These include the X.org and Wayland graphical servers, the GStreamer multimedia framework, many GNOME projects and even systemd. Without fail these project report that the change has brought about many improvements such as reduced compile times and increased agility, easier to understand build definitions and, perhaps most importantly, increased developer happiness.\r\n\r\nIn this talk we shall be looking into the design and features of Meson and how they improve the day-to-day developer experience. These improvements are undeniable and noticeable but it turns out that selling them to people was a lot tougher than it first seems. We shall go through the history of Meson and talk about the all the hidden work behind the scenes that needed to be done before projects were willing to transition off their tried and true build systems to the new shiny.\r\n\r\nThe presentation concludes by looking at new features that are in the pipeline and other new things we should expect from Meson in the future.',
