        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'The RomCom, the App and the Wardrobe',
        authors: ['Hannah Thompson'],
        abstract: '22 years ago, the coming-of-age romantic comedy film Clueless introduced teenage girls (amongst others) to the possibility of using computers and artificial intelligence for fashion. The main character, status obsessed Cher, uses her classic Mac (with added touchscreen) to swipe through her virtual wardrobe and create a real outfit. This movie, a modern remake of a Jane Austen classic, looked into the future with it\'s wardrobe app - and has become the inspiration for many actual clothes management software projects. For example, in 2017, Amazon released Echo Look - a robot that hides in your wardrobe and judges your clothing.\r\n\r\nThis talk will discuss the creation of a modern, open source app using open source tools, through the lens of it\'s Clueless inspiration. Utilising Facebook open source tools React Native and Redux, Jules\' Wardrobe is a recreation of Cher\'s Wardrobe. While Cher and Jane Austen could only have dreamed of holding their wardrobes in their hands, it is now a possibility.\r\n\r\nAudience takeaways will include an overview of creating mobile phone applications with open source tools, a better understanding of the potential for using technology with fashion, and an overall sense of nostalgia for 90s movies.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/43/',
        cancelled: false,
        twitter_id: 'hannahcancode',
