        track: null,
        conf_key: 84,
        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Device Tree: Past, Present, and Future',
        authors: ['Neil Armstrong'],
        abstract: 'Since the switch of the ARM Linux support from the stable PowerPC Device Tree support, it became an important piece of software used to describe all sorts of devices based on very different hardware architectures.\r\nCurrently, BSD* Unixes and even the Zephyr RTOS has switched to Device Tree to describe the hardware. U-boot has also a file format using the Device Tree blob format.\r\nNeil will present you the history of Device Tree from its origins, how it has been used for ARM from the PowerPC codebase, all the very different current usage and an overview of its future application and evolutions.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/24/',
        cancelled: false,
        twitter_id: 'superna9999',
