        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Self-Documenting Coders: Writing Workshop for Devs',
        authors: ['Heidi Waterhouse'],
        abstract: 'Being able to use language effectively saves so much coding trauma. You should learn to file good bug reports, write up problems, and describe what you\'re doing. I\'ll teach you in less than half a day!\r\n\r\nWhat is a documentation structure, and why does it matter to developers? \r\nLots of developers get asked to write their own documentation, especially internal documentation and onboarding. In theory, this is good because they know the problems they are writing about and don\'t need to spend time explaining them. In practice, developers avoid this work because they don\'t have a good idea of how to start and can\'t evaluate whether they have succeeded. \r\n\r\nThis workshop is designed to teach you a few basic theories of technical documentation, such as task-based topics, reusable content, and writing for an audience. After the overview, you\'ll learn techniques for writing bug reports, error messages, and onboarding instructions in a tool-agnostic, repeatable way. You\'ll leave this workshop with a handful of techniques, templates, and tests that will improve your team\u2019s communication and your life as a developer.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/99/',
        cancelled: false,
