        name: 'ReasonReact - Functional Programming for React Developers',
        authors: ['Keira Hodgkison'],
        abstract: 'ReasonReact is a safer, simpler way to build React components, in Reason. Created by developers at Facebook, Reason is a syntax layer and toolchain for OCaml. With over 1.7 million downloads a week, React is a well established and popular tool to create component based user interfaces. By enabling React developers to leverage their existing knowledge and ecosystem, ReasonReact brings functional programming for front-end development, one step closer to widespread adoption.\r\n\r\nSince its release in March 2017, ReasonReact has progressed quickly. In this brief overview, attendees should gain an understanding of;\r\n\r\n- What ReasonML/ReasonReact is, \r\n- How the language has evolved,\r\n- Some highs and lows of integrating it into an existing React application - especially when you have little experience in functional programming or type systems.',
