        name: 'Wearing access: a story about open collections, a sewing machine and the nation\u2019s secrets.',
        authors: ['Bonnie Wildie'],
        abstract: 'This presentation is a demonstration of what happens when you take inspiration from a 19th century dress, mix it with online guides about getting crafty with open GLAM collections, and add an invitation to get creative with some of the nation\u2019s secrets. The \u201cPress Dress\u201d, worn by Matilda Butters in 1860s Melbourne, proclaimed the Liberty of Press at the time, and has, as time passes, become a time capsule that holds stories and histories from the period. The ability to wear history is an appealing and powerful notion. As the GLAM sector opens its collections and enables reuse, the ability to create powerful statements using historical documents is more readily realised. Online guides by digital humanists provide advice and guidance about accessing and reusing open GLAM collections in crafty and creative ways, utilising DIY culture and DIY tools for potentially subversive actions. An open invitation to reuse images of ASIO redactions provided an opportunity to utilise these DIY tools and in the process challenge the idea of access. This story begins and ends with a dress.',
