        name: 'Source Code Generation: How I Learned to Stop Worrying and Love the Boilerplate',
        authors: ['Daniel Phillips'],
        abstract: 'Automatic source code generation might seem like a hack to most developers, but it doesn\'t have to be this way. When used effectively as part of a software project\'s development and build processes, code generation can encourage good practices, reduce bugs, and save valuable developer time by eliminating the need to write boilerplate code by hand. This talk will look at a few use cases for automatic source code generation, and discuss the high-level design principles involved in using, or even rolling your own source code generator.',
