        kind: 'talk',
        section: 'main',
        section_name: 'Main Conference',
        track: null,
        conf_key: 51,
        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'RancherOS, a golang init for containerized services',
        authors: ['Sven Dowideit'],
        abstract: 'Containers need a host and an OS on which they run. Even "Server-less"\r\nRancherOS was created from the idea that everything could be a container - so it has a System container service, and once enough system services have started, starts up a "User Docker" container.\r\nIn this talk, I\'ll be discussing the fun of writing a "simple" Linux init process, which uses cloud-init and container composition to bootstrap the OS, and the user\'s containers.\r\nAnd then there\'s the testing and maintenance - "it doesn\'t work on my IBM Zwave 52-zxvc - but its fine on the other one."',
