        contact: ['redacted'],
        name: 'nftables from a user perspective',
        authors: ['Duncan Roe'],
        abstract: 'nftables replaces the popular {ip,ip6,arp,eb}tables Firewall and Router utilities.\r\nFor some years its status had been "Under Development", but this changed in early 2017.\r\nThis talk explores the ease of transition from iptables, and the advantages and drawbacks of nftables.',
