        contact: ['redacted'],
        name: 'The State of Kernel Self-Protection',
        authors: ['Kees Cook'],
        abstract: 'The Kernel Self-Protection Project focuses on addressing gaps in Linux\'s defensive technologies. With Linux reaching into every corner of modern life, and userspace frequently being very locked-down, the kernel has become an ever-increasing target for attackers and much more needs to be done to harden the kernel so it can protect itself. A quick overview will be shown of what we\'re trying to protect Linux against, as well as the state of the art in available technologies. Also presented will be a summary of the last year\'s participation by many people over a wide range of technologies, with a review of KSPP attempts, accomplishments, active efforts, and an examination of future projects and goals.',
