        name: 'On Writing Machines',
        authors: ['Mark Rickerby'],
        abstract: 'From the earliest days of computing, people have tried to devise programs to generate comprehensible and authentic creative writing. From the weird chaos of spam email, to infinite story games, art bots and entire novels generated from code, the core problem in developing writing machines is how to generate surprising and expressive texts that are different each time the program runs. We\u2019ll explore the emerging field of generative writing through a unique conceptual framework that draws on contrasting\u2014and sometimes contradictory\u2014insights and patterns from computational linguistics, game design and traditional literature.',
