        contact: ['redacted'],
        name: 'In from the cold: Finally making Linux and .NET get along',
        authors: ['Alistair Chapman'],
        abstract: '.NET Core has finally arrived and is soon to hit 2.0, bringing vastly improved Linux compatibility and support for more distributions. Has the promised age of truly cross-platform .NET and real Linux support finally come?\r\n\r\nThis talk will be drawing on years of experience working with .NET technologies on Linux platforms (including Mono), as well as working with the latest .NET Core stack. In this session, I will be covering a brief history of .NET on Linux bringing us to today\u2019s new cross-platform stack, including how to get started with .NET Core on Linux platforms.\r\n\r\nI\u2019ll also be looking at how you can integrate .NET apps and services into Linux environments, including simple deployment, testing and packaging strategies, as well as looking at how to integrate .NET with common Linux services including systemd, dbus and GTK. Finally, I will be showing how to build a complete pipeline to build, deliver and run .NET apps entirely on Linux.\r\n\r\nThis talk will start with the high-level essentials of working with .NET technologies on Linux while also covering the deeper technical details so is perfect for any level of Linux user, but will be especially useful to .NET users.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/121/',
        cancelled: false,
