        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Status of Mainline Linux on AmLogic SoCs',
        authors: ['Neil Armstrong'],
        abstract: 'Inexpensive set-top boxes are everywhere and many of them are powered by AmLogic SoCs. These chips provide 4K H.265/VP9 video decoding and have fully open source Linux kernel and U-boot releases.\r\nUnfortunately most of the products based on these devices are running an ancient 3.10 Android kernel. Thankfully AmLogic has put a priority on supporting their chips in the mainline Linux kernel.\r\nNeil will present the challenges and benefits to pushing support for these SoCs upstream, as well as the overall hardware architecture in order to understand the Linux upstreaming decisions and constraints.\r\nNeil will also detail the future development plans aiming to offer a complete experience running an Upstream Linux kernel.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/158/',
        cancelled: false,
