        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'You Should Speak',
        authors: ['E. Dunham'],
        abstract: 'You\'ve been hearing from presenters from all walks of life during this conference, and perhaps it\'s made you wonder: Could you be one, too? You might have a topic in mind that you\'d love to share with others, or you could still be undecided on what to speak about. Either way, I\'ll demystify the process of writing a talk, submitting it to the right calls for papers, and making sure everyone in the audience leaves the room knowing something new.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/178/',
        cancelled: false,
        twitter_id: 'qedunham',
