        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Monitoring All the Things! on your Linux system with the Elastic Stack',
        authors: ['Josh Rich'],
        abstract: 'In this talk, we\'ll look at how you can easily ingest your Linux system logs and various OS metrics into Elasticsearch using Filebeat and Metricbeat modules.  Modules are a new concept in the open-source Filebeat and Metricbeat tools made by Elastic. We can then visually examine both our systems performance and all events occurring on it over time with Kibana. This is a near complete open source monitoring solution for a Linux system.  \r\n\r\nAssuming the demo gods allow, We\'ll have a little bit of a play with our systems, by inducing CPU/memory load or spamming log lines to see it reacts in Kibana, and correlate the different sources of information together in a single Kibana dashboard, providing a relatively complete view of what is happening on the system.  \r\n\r\nFinally, anything missing we want to monitor or record we can do by writing our own Filebeat or Metricbeat module. So we will take a dive into the code to see how you can contribute your own Filebeat or Metricbeat module to these projects.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/161/',
