        contact: ['redacted'],
        name: 'Augmenting Reality with Open Source',
        authors: ['Eloise Ducky'],
        abstract: 'Wanna make some cool Augmented Reality (AR) games? Can\'t afford commercial AR libraries or just want to use open source because you\'re cool like that? Well this talk is here for you!\r\nCome hear about some great open source resources for making applications with AR, as well as some design tips about how not to make AR just another gimmick.',
