        name: 'Manage all your tasks with TaskWarrior',
        authors: ['Paul \'@pjf\' Fenwick'],
        abstract: 'TaskWarrior is a free, open source, cross-platform command-line tool for managing tasks. TaskWarrior is extremely extensible, flexible, and suits a diverse range of workflows. We\'ll examine how to use TaskWarrior to manage *all* your tasks, and how to configure it so your to-do list remains manageable even when your task list gets large.\r\n\r\nIn particular, we will examine:\r\n\r\n- Getting started with a simple workflow\r\n- Setting up useful reports\r\n- Managing multiple projects\r\n- Integration with Github, JIRA, Gmail, and Trello (and more!)\r\n- Putting your task-list in your shell prompt\r\n- Writing your own TaskWarrior extensions',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/115/',
        cancelled: false,
        twitter_id: 'pjf',
