        name: 'Come on; do you want your mods to live forever?',
        authors: ['Leon Wright'],
        abstract: 'With Kerbal Space Program mods being created and uploaded at a rapid pace, we were already seeing some go missing. As the CKAN (Comprehensive Kerbal Archive Network) was already automatically indexing them and seeing that the overwhelming majority of mods were FOSS licensed, it made sense to automate the process of submitting them. As of this writing over 11,000 Kerbal Space Program mods from over 700 authors have been archived to Archive.org. This talk will cover the process of how the historical mods were archived and the automation of new additions.',
