        name: 'Icinga 2 in a 24/7 Television Broadcast Environment',
        authors: ['Dave Kempe'],
        abstract: 'Icinga2 is gaining steam and is much improved compared to it\'s ancestor\'s Nagios and Icinga1. I will present some war stories and implementation details from our Icinga deployments into television broadcast environments as a template for anyone using Icinga in critical environments with obscure hardware and software. From plugins we needed to develop, to challenges in effecting change in staff practices I will walk through the projects and share my experiences on the way. We will cover the implementation of distributed monitoring in Icinga2 with strict firewalls, building dashboards using Nagvis and integration of Opsgenie for alerting.',
