        contact: ['redacted'],
        name: 'Functional DevOps in a Dysfunctional World',
        authors: ['Vaibhav Sagar'],
        abstract: 'What happens after you cut a release? Getting your finished software in the hands of your users can be quite an ordeal, especially if you use tools that make things harder than they need to be. It turns out that a functional approach has benefits even after the development team has written their last line of code.\r\n\r\nI will motivate and demonstrate using the Nix suite of tools to package and deploy a web service, showing how this helps us reconcile the sometimes conflicting priorities of development and operations teams and allows us to respond nimbly to change as it occurs.\r\n\r\nI hope to leave you with the assurance that Nix is production-ready and the means to start using it yourself.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/153/',
