        section: 'main',
        section_name: 'Main Conference',
        track: null,
        conf_key: 23,
        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Should you rewrite in Rust?',
        authors: ['E. Dunham'],
        abstract: 'When coders hear about a promising programming language, they often think "I\'d like to write something in that!" While rewriting an existing utility or codebase in a new language can sometimes be a good idea, there are many situations in which it\'s not -- and to make it even more difficult, the benefits and drawbacks of a rewrite vary greatly based on which languages you\'re using! \r\n\r\nCome to this talk to learn about the various techniques you can use to integrate Rust with existing code in other languages, their pros and cons, and who\'s using them in the industry. Even if you haven\'t tried Rust yet, you\'ll get a crash course in its strengths, its weaknesses, and the many ways you can make it do cool stuff.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/98/',
        cancelled: false,
