        name: 'Open Source in the FIRST Robotics Competition',
        authors: ['David Vo'],
        abstract: 'There\u2019s no better way of getting people interested in STEM fields than cool outreach programs, especially high school outreach programs.\r\n\r\nThe FIRST Robotics Competition (FRC) is one of these important high school outreach programs.  This talk will explore what FRC involves, the role that open source plays within FRC, and the various open source communities built around FRC.',
