        conf_key: 175,
        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Principles Of Good Monitoring',
        authors: ['Troy Lea'],
        abstract: 'Deploying a monitoring system can be a daunting process. If you\'re starting out from scratch you\'ll come across options like active vs passive, performance metrics, log files, bandwidth and SNMP. You can easily get lost trying to make sense of it all. This talk will discuss why each method exists and what open source solutions are available for each method. Nagios Core and its open source modules will be referenced in the talk, however other open source agents used by Nagios will also be discussed. Log server monitoring will discuss the ELK stack. Netflow monitoring will discuss nfcap. The topics covered in this talk can be applied to any open source monitoring system.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/165/',
        cancelled: false,
