        released: true,
        contact: ['redacted'],
        name: 'Quick Introduction to OpenNMS',
        authors: ['Tarus Balog'],
        abstract: 'OpenNMS is the most powerful network monitoring platform of which you\'ve never heard about. Started in 1999, the focus of OpenNMS is scalability, with its aim to be the de facto network monitoring platform for the Internet of Things.\r\n\r\nThis short talk with cover the high points of OpenNMS, from managing events, collecting data, monitoring services and provisioning the system at scale. It will also touch on upcoming features included flow analysis and bringing machine learning to network monitoring.\r\n\r\nOpenNMS is 100% free and open source software (there is no "enterprise" version with extra features) and it is used by some of the largest organizations in the world. Come see if OpenNMS is a good fit for yours.',
