        name: 'GPL compliance report and Q&A',
        authors: ['Karen Sandler'],
        abstract: 'Karen will give an overview on where things are for GPL compliance around the Linux kernel. She will report on Conservancy\'s own enforcement activities and give an overview of some of the key developments over the past year, like the Linux Kernel Enforcement Statement.  The session will include a significant amount of time for Q&A to discuss licensing questions, the Principles of Community-Oriented GPL enforcement and anything else that seems most relevant on the day.',
