        conf_key: 272,
        license: 'CC BY',
        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'Digitise the Dawn - 5 years on',
        authors: ['Donna Benjamin'],
        abstract: 'At a miniconf in 2011 I announced a campaign to Digitise the Dawn*. We raised the cash, the National Library digitised the journal, and it\'s been available online in Trove for more than 5 years. So now what? \r\n\r\n- What lessons can we learn from the campaign?\r\n- How can libraries, archives and communities work together to increase access to past knowledge?\r\n- What can and should we do with this knowledge once it\'s freely available?\r\n- How do we ensure it lives on if the institutions who host it are defunded?\r\n- Can we ask and answer these and other questions in this session?\r\n\r\nPart storytime, part discussion and debate, this session will explore the circus at the intersection of activism, culture and technology.\r\n\r\n*Wait. What\'s "Digitise the Dawn"? See http://digitisethedawn.org/australian-feminist-studies-report',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/175/',
        cancelled: false,
        twitter_id: 'kattekrab',
