        name: 'Burning Down the Castle',
        authors: ['Daniel Vetter'],
        abstract: 'My retrospective on 5 years of Linux kernel maintainership: Why the kernel community is a lot more broken than it looks, why maintainers benefit from that and why it\'s unlikely to change.\r\n\r\nJust dissecting the gruesome workings of a broken community might be entertaining, but not all that enlightening. This talk will also look at how well-built communities, both large&small, avoid these pitfalls.',
