        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'IPv6 and Containers: Why We Can\'t Have Nice Things (And How We Can)',
        authors: ['Matt Palmer'],
        abstract: 'Containers are everywhere.  And they all need IP addresses.  IPv6 has lots and lots of IP addresses.  It\'s a match made in heaven!  Despite this perfect match of problem and solution, however, there are a number of challenges that you\'ll face if you try to use IPv6 to address your containers.\r\n\r\nWith the benefit of having struggled (and mostly succeeded) to make containers and IPv6 play well together for the last two years, Matt Palmer has a lot of war stories and useful tips to make your journey to container networking nirvana a bit smoother.  He\'ll describe why things are the way they are, why they don\'t have to be that way, and how you, too, can experience the joys of never again having to configure a Docker port forward.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/49/',
