        contact: ['redacted'],
        name: 'MQTT as a Unified Message Bus for Infrastructure Services',
        authors: ['Matthew Treinish'],
        abstract: 'Development and testing of the OpenStack project operates at a tremendous scale, with hundreds of code repositories and thousands of contributors interacting continuously. The infrastructure to support this has to operate at an equally large scale to ensure that it is not outpaced by the volume of upstream development activity. Enabling users and other consumers to see what is happening in real time in this increasingly complex infrastructure becomes equally complex and large. This is why we need interfaces available to develop tooling to handle this.\r\n\r\nMQTT best known for it\'s use in IoT and sensor network applications also provides a number of advantages when used as an event bus for infrastructure services. For the OpenStack developer and community infrastructure we introduced firehose.openstack.org to provide a unified message bus. Built using MQTT and Mosquitto, firehose is an interface where services can publish events.\r\n\r\nThis talk will cover how you can use MQTT as a unified event bus for infrastructure services. It will explain some basics of the MQTT protocol and why it\'s well suited for this application. It will also use the OpenStack community infrastructure\'s firehose as a case study to explain the benefits and how a similar system can be used for their your own needs, experimentation and innovation.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/168/',
        cancelled: false,
