        name: 'Lost artist in the world of technology',
        authors: ['Karynne Ledger'],
        abstract: 'How can the artist incorporate new technology, still be a craftsperson, and still feel justified in the creation?\r\nAs an artist, chef and neophyte when it comes to technology, I questioned what some ways artists continue to hold onto  their skills and redefine them using new technologies alongside traditional mark making processes. Some of these technologies make life easier and others a little more challenging, especially for the non techie, or tech geek.\r\nFrom these thoughts, I will be discussing some of my past projects and challenges, alongside new challenges in my new pieces.',
