        contact: ['redacted'],
        name: 'From Software Carpentry to Library Carpentry',
        authors: ['Clinton Roy'],
        abstract: 'Software Carpentry is group that teaches fundamental software engineering skills to researchers, to enable them to make full use of today\u2019s computing capabilities, both to increase the rate of research, and improve the reproducibility of their research. All of the lessons are created under Open Source licences. Software Carpentry also undergoes constant internal reflection to improve their teaching practices.\r\n\r\nThere are many offshoots of Software Carpentry focusing on different research areas, such as big data and biology. There is even a nascent project focusing on librarianship: Library Carpentry.\r\n\r\nThis talk will explore Software Carpentry underpinnings and look towards what lessons and teaching methods make sense for the GLAM industries.',
