        contact: ['redacted'],
        name: 'Building Indie Games through Open Source',
        authors: ['Jon Manning'],
        abstract: 'Open source is community-driven, while independent video games tend to focus on very small teams or individuals. While it\'s never been easier to get access to development tools, like Unity, Cocos 2D, or Godot, the act of creating a game remains a huge amount of effort. Getting others to help can be the difference between shipping something great, and an abandoned project.\r\n\r\nIn this talk, we\'ll talk about how open-sourcing chunks of your game during development can both help your fellow game devs, and help you build your own game faster. We\'ll talk about how Night in the Woods (2017) open-sourced parts of itself during development, and discuss a workflow for continuously open-sourcing parts of your game during production.',
