        tags: '',
        released: true,
        contact: ['redacted'],
        name: 'From Blink to Throb - an exploration of hobbyist lighting projects',
        authors: ['Peter Hardy'],
        abstract: 'The `Hello World` of the Arduino ecosystem is the `Blink` sketch, a single light blinking on and off at 0.5Hz. Most Arduino compatible boards include a built-in LED just to run it. But it\'s capable of an awful lot more.\r\n\r\nIn this talk I\'ll introduce some options for creating bigger, better, funner light art using Arduino and Raspberry Pi controllers. I\'ll discuss some popular maker projects, and demo one or two of my own.',
        conf_url: 'https://rego.linux.conf.au/schedule/presentation/146/',
        cancelled: false,
        twitter_id: 'stibbons',
