        contact: ['redacted'],
        name: 'Samba AD at Scale: progress, pitfalls and forgetting to take out read locks',
        authors: ['Andrew Bartlett'],
        abstract: 'Samba 4.7 brings a massive improvement in stability and scale, and even larger organisations are deploying it regularly.  \r\n\r\nThis talk will update system administrators on the current status in Samba, the locking issues we addressed with Samba 4.7, the new scale of organisation that Samba can now address and the tools we have written to test Samba at scale.',
