      'At the ZKM (Center for Art and Media) new media, education, and innovation are at the heart of its purpose. One of its latest exhibits not only encapsulates these, but has an open source edge to boot. \r\nOpen Codes is more than just a collection of technology-based art; It is an experiment in understanding and engagement of our digital world. \r\nThis talk will cover the open codes exhibit; how and what it aims to do in terms of engagement, collaboration, and education. This will feature discussions of the unique points in its collection, use of open source tools, open culture, and potential differences in the European environment.',
